.items-category {
  font-size: 1.8rem;
  line-height: 1.222;
  margin-bottom: 1.8rem;
}

.items-category h1 {
  margin-bottom: 1.8rem;
}

.items-category:not(:last-child) {
  margin-bottom: 4.753rem;
}

.items-list {
  display: flex;
}

.item {
  width: 18.2rem;
  height: 5rem;

  background: #ffffff;
  box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.05);
  border-radius: 12px;

  display: flex;
  align-items: center;
  padding-left: 1.6rem;
  cursor: pointer;
}

.item:not(:last-child) {
  margin-right: 1.961rem;
}
