/* .shopping-list-container {
} */

.shopping-list-title {
  margin: 0 3.186rem 3.934rem 4.839rem;
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  font-size: 2.4rem;
  line-height: 1.25;
  /* identical to box height */
  color: #34333a;
}

.shopping-items-container {
  margin: 0 4.5rem 5.14rem 4.74rem;
}

.shopping-list-category {
  margin-bottom: 2.5rem;
  font-size: 1.4rem;
  line-height: 1.214;
  /* identical to box height */

  color: #828282;
}

.shopping-item-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3.38rem;
  position: relative;
  height: 3.2rem;
}

.shopping-item-container:last-child {
  margin-bottom: 0;
}

.shopping-item {
  font-size: 1.8rem;
  line-height: 1.222;
  /* identical to box height */

  color: #000000;
}

.shopping-quantity-button {
  color: #f9a10a;
  background: transparent;
  width: 6.8rem;
  height: 3.2rem;
  border: 2px solid #f9a109;
  box-sizing: border-box;
  border-radius: 24px;
  cursor: pointer;
}

.shopping-quantity-button:focus {
  outline: none;
}
