.left-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  background-color: #ffffff;
  flex: 0 0 6.522%;
  height: 100vh;
}

.logo-icon {
  margin-top: 3.843rem;
  /* margin-bottom: 26.667rem; */
}

.icons-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 20.6rem;
  width: 100%;
}

.icon-container {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 4.598rem;
  position: relative;
}

.icon-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 6px;
  background-color: #f9a109;
  border-radius: 0px 4px 4px 0px;
  opacity: 0;
}

.icon-container:hover::before {
  opacity: 1;
}

.cart-container {
  margin-bottom: 3.486rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 4.2rem;
  height: 4.2rem;
  background-color: #f9a109;
  border-radius: 100%;
}
