.edit-menu-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #ffffff;
  border-radius: 12px;
  position: absolute;
  right: 0;
  height: 4.49rem;
}

.remove-icon {
  margin: 0 0.4rem 0 0.8rem;
  cursor: pointer;
  border-radius: 24px;
}

.remove-icon:hover {
  background: #f2f2f2;
}

.delete-item-button {
  background: #f9a109;
  border-radius: 12px;
  border: none;
  height: 100%;
  cursor: pointer;
}
.delete-item-button:focus {
  outline: none;
}

.add-icon {
  margin: 0 0.4rem 0 0.4rem;
  cursor: pointer;
}

.add-icon:hover {
  background: #f2f2f2;
  border-radius: 24px;
}
