.button-container {
  height: 6.125rem;
  border: none;
  border-radius: 12px;
  font-size: 1.6rem;
  line-height: 1.25;
  font-weight: bold;
  padding: 2rem 2.5rem;
  background-color: transparent;
  cursor: pointer;
}

.button-container:not(.button-yellow):hover {
  background-color: #e1e1e4;
}

.button-container:focus {
  outline: none;
}

.button-yellow {
  background-color: #f9a109;
  color: #ffffff;
}

.buttons-container {
  display: flex;
  justify-content: space-evenly;
}
