.itemdetails {
  background-color: #ffffff;
  flex: 0 0 27.013%;
  padding: 2.784rem 4.446rem 3.486rem 4.411rem;
}

.back-container {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-bottom: 3.4rem;
}

.back-paragraph {
  margin-left: 0.65rem;
  color: #f9a109;
  font-weight: bold;
  font-size: 1.4rem;
  line-height: 1.214;
}

.arrow-icon {
  fill: #f9a109;
  transform: matrix(-1, 0, 0, 1, 0, 0);
}

.item-image {
  background-position: center center;
  background-size: cover;
  width: 30rem;
  height: 21.9rem;
  border-radius: 25px;
  margin-bottom: 5.368rem;
}

.itemdetail-titles {
  font-size: 1.2rem;
  line-height: 1.25;
  /* identical to box height */
  margin-bottom: 1.15rem;
  color: #c1c1c4;
}

.itemdetail-paragraph:first-child {
  font-size: 2.4rem;
  line-height: 1.25;
  /* identical to box height */

  color: #000000;
}

.itemdetail-paragraph {
  margin-bottom: 3.3rem;
  font-size: 1.8rem;
  line-height: 1.222;
  /* identical to box height */

  color: #000000;
}
