.additeminput-container:not(:last-child) {
  margin-bottom: 1.8rem;
}

.additeminput-container:last-child {
  margin-bottom: 1.215rem;
}

.additeminput-container > p {
  font-size: 1.4rem;
  line-height: 1.214;
  margin-bottom: 0.6rem;
}

.additeminput-container > input {
  padding-left: 1.73rem;
  font-size: 1.4rem;
  line-height: 1.214;
  width: 100%;
  height: 6.25rem;
  border: 2px solid #bdbdbd;
  /* box-sizing: border-box; */
  border-radius: 12px;
}

.additeminput-container > input:focus {
  border: 2px solid #f9a109;
  outline: none;
}

.additeminput-container > input::placeholder {
  color: #bdbdbd;
}

.big {
  height: 11.22rem !important;
  padding-bottom: 51px;
}
